import React, { Component } from 'react';
import { Avatar, Dropdown, Menu, Badge } from 'antd';
import Websocket from 'react-websocket';
import {
  UserOutlined,
  LogoutOutlined,
  MenuUnfoldOutlined,
  SettingOutlined,
  CaretDownOutlined,
  BellOutlined,
  WindowsOutlined,
  IdcardOutlined
} from '@ant-design/icons';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { setUserInfo } from '@/redux/actions/userInfo';
import { emptyTag, addTag } from '@/redux/actions/tagList';
import {
  setCollapse,
  setBreadCrumb,
  setTags,
  setTheme
} from '@/redux/actions/setting';
import { routes } from '@/router/routes';
import FullScreen from '@/components/FullScreen';
import Tags from './Tags';
import BasicDrawer from '@/components/BasicDrawer';
import styles from './index.scss';

class TopHeader extends Component {
  state = { visible: false, num: '' };
  handleLogout = () => {
    this.props.setUserInfo({});
    this.props.emptyTag();
    localStorage.removeItem('isLogin');
    localStorage.removeItem('userInfo');
    this.props.history.push('/');
  };
  componentDidMount() {
    let userInfo =
      localStorage.getItem('userInfo') &&
      JSON.parse(localStorage.getItem('userInfo'));
    if (userInfo) {
      this.props.setUserInfo(userInfo);
    } else {
      this.props.setUserInfo({});
      this.props.history.push('/login');
    }
  }
  toNews = () => {
    this.handClickTag('/bell');
    this.props.history.push('/bell');
  };
  handClickTag(path, parent) {
    for (let i = 0; i < routes.length; i++) {
      if (path === routes[i].path) {
        let obj = { path, title: '消息', component: routes[i].component };
        this.props.addTag(
          parent ? Object.assign({}, obj, { parent: parent.title }) : obj
        );
      }
    }
  }
  toggle = () => {
    this.props.setCollapse({ isCollapsed: !this.props.collapse.isCollapsed });
  };
  setting = () => {
    this.setState({ visible: true });
  };
  onClose = () => {
    this.setState({ visible: false });
  };
  onChangeTags = checked => {
    this.props.setTags({ show: checked });
    localStorage.setItem('tags', JSON.stringify({ show: checked }));
    this.onClose();
  };
  onChangeBreadCrumb = checked => {
    this.props.setBreadCrumb({ show: checked });
    localStorage.setItem('breadCrumb', JSON.stringify({ show: checked }));
    this.onClose();
  };
  onChangeTheme = checked => {
    this.props.setTheme({ type: checked ? 'dark' : 'light' });
    localStorage.setItem(
      'theme',
      JSON.stringify({ type: checked ? 'dark' : 'light' })
    );
    this.onClose();
  };
  // 处理bellws
  handleBellNum = num => {
    if (!num.includes('连接成功')) {
      this.setState({
        num
      });
    }
  };
  render() {
    const { id: uid } =
      localStorage.getItem('userInfo') &&
      JSON.parse(localStorage.getItem('userInfo'));
    const DropdownList = (
      <Menu className='drop-list'>
        <Menu.Item
          key='userInfo'
          onClick={() => {
            this.props.history.push('./userInfo');
          }}
        >
          <IdcardOutlined />
          个人资料
        </Menu.Item>
        {/* <Menu.Item key='user'>
          <UserOutlined />
          {Object.keys(this.props.userInfo).length > 0 &&
            this.props.userInfo.role.name}
        </Menu.Item> */}
        <Menu.Item key='logout' onClick={this.handleLogout}>
          <LogoutOutlined />
          退出登录
        </Menu.Item>
      </Menu>
    );
    const { tags } = this.props;
    return (
      <div className='top-header'>
        <Websocket
          url='ws://localhost:8088/bellWs'
          onMessage={() => {}}
          reconnectIntervalInMilliSeconds={10000}
          sendMessage='111'
        />
        {/* 未读消息条目 */}
        <Websocket
          url={`ws://localhost:8088/bellNumWs/uid=${uid}`}
          onMessage={this.handleBellNum}
          reconnectIntervalInMilliSeconds={10000}
          sendMessage='111'
        />
        <div className='top-header-inner'>
          <MenuUnfoldOutlined className='trigger' onClick={this.toggle} />

          <div className={`${styles.top} header-title`}>
            <span>
              Hi&nbsp;
              {JSON.parse(localStorage.getItem('userInfo')).userName || ''}~
            </span>
            <span>大规模虚拟集群管理系统运行状态良好~</span>
          </div>
          <div className='header-right'>
            <div className='full-screen'>
              <FullScreen />
            </div>
            <div className='setting'>
              <SettingOutlined
                style={{ fontSize: '21px', cursor: 'pointer' }}
                onClick={this.setting}
              />
            </div>
            <div className='news-wrap'>
              <Badge count={this.state.num}>
                <BellOutlined
                  style={{ fontSize: '20px', cursor: 'pointer' }}
                  onClick={this.toNews}
                />
              </Badge>
            </div>
            <div
              className='dropdown-wrap'
              id='dropdown-wrap'
              style={{ cursor: 'pointer' }}
            >
              <Dropdown
                getPopupContainer={() =>
                  document.getElementById('dropdown-wrap')
                }
                overlay={DropdownList}
              >
                <div>
                  <Avatar
                    size={32}
                    src='data:image/png;base64,'
                  />
                  <CaretDownOutlined
                    style={{ color: 'rgba(0,0,0,.3)', cursor: 'pointer' }}
                  />
                </div>
              </Dropdown>
            </div>
          </div>
        </div>
        {tags.show ? <Tags /> : null}
        <BasicDrawer
          title='系统设置'
          closable
          onClose={this.onClose}
          visible={this.state.visible}
          onChangeTags={this.onChangeTags}
          onChangeBreadCrumb={this.onChangeBreadCrumb}
          onChangeTheme={this.onChangeTheme}
          {...this.props}
        />
      </div>
    );
  }
}

const mapStateToProps = state => state;
const mapDispatchToProps = dispatch => ({
  setCollapse: data => {
    dispatch(setCollapse(data));
  },
  setUserInfo: data => {
    dispatch(setUserInfo(data));
  },
  emptyTag: () => {
    dispatch(emptyTag());
  },
  addTag: data => {
    dispatch(addTag(data));
  },
  setBreadCrumb: data => {
    dispatch(setBreadCrumb(data));
  },
  setTags: data => {
    dispatch(setTags(data));
  },
  setTheme: data => {
    dispatch(setTheme(data));
  }
});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(TopHeader));
